<template>
  <main>
    <div class="container-fluid">
      <!-- START: Breadcrumbs-->
      <div class="d-block header-section">
        <div class="">
          <h1>
            <b>Return Order - View</b>
          </h1>
        </div>
      </div>
      <!-- START: Card Data-->
      <div class="row">
        <!-- END: Card DATA-->
      </div>
      <!--START : Trace Pakage Carton-->
      <div class="card mb-4">
        <div class="row p-5">
          <div class="col-12 col-md-4 p-3 row">
            <div class="col-md-3">
              <img src="@/assets/images/icons/real-code-image.jpg" />
            </div>
            <div class="col-md-9 mt-2">
              <h5 class="text-muted">Order Number</h5>
              <h5 class="d-flex align-items-left text-primary">
                {{ orderDetails.number }}
              </h5>
            </div>
          </div>
          <div class="col-12 col-md-4 border-left p-3 row">
            <div class="col-md-3">
              <img src="@/assets/images/icons/return-status-image.jpg" />
            </div>
            <div class="col-md-9 mt-2">
              <h5 class="text-muted">Status</h5>
              <h5 class="d-flex align-items-left text-primary">
                {{ codeStatus[orderDetails.returnOrderStatus] }}
              </h5>
            </div>
          </div>

          <div class="col-12 col-md-4 border-left p-3 row">
            <h5 class="d-flex align-items-left text-primary"></h5>
          </div>
        </div>
      </div>

      <div class="d-block header-section">
        <div class="float-left">
          <h3>
            <b>Order Details</b>
          </h3>
        </div>
      </div>

      <div class="col-12 col-sm-12 mt-3 p-0">
        <div class="table-responsive">
          <table class="table layout-secondary return-order bordered">
            <thead>
              <tr>
                <th>Package Type</th>
                <th>Product Type</th>
                <th>No of Real</th>
                <th>Real Size</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="packagedt in orderDetails.packageDtos"
                :key="packagedt.packageId"
              >
                <td>{{ packageType[packagedt.packageType]}}</td>
                <td class="ImporDome">Alcohol Domestic</td>
                <td>{{ packagedt.reelCount }}</td>
                <td>{{ packagedt.stampCount }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <!--END : Trace Pakage Carton-->
      <div class="card mb-4">
        <div class="row p-0">
          <div class="col-12 col-md-4 p-3 ml-3">
            <b
              >Delivery Partner :
              <span>{{ orderDetails.warehouse.warehouseName }} </span></b
            >
          </div>
        </div>
      </div>
      <!-- orderDetails.returnOrderStatus -->
      <div class="col-md-4 mr-4" v-if="orderDetails.returnOrderStatus == 3">
        <div class="form-group">
          <label class="col-form-label mr-2"><b>Comment</b></label>
          <div class="">
            <textarea
              class="form-control"
              rows="5"
              v-model="comment"
            ></textarea>
          </div>
        </div>
      </div>
      <div class="" v-if="orderDetails.returnOrderStatus == 3">
        <button
          type="button"
          class="btn btn-primary p-2 pl-5 pr-5"
          @click="approveReturnOrder"
        >
          Approve
        </button>
      </div>
      <div class="col-md-12 float-right">
        <router-link :to="{ name: 'ReturnOrderListing' }">
          <button
            type="button"
            class="btn btn-primary float-right mt-3 mb-5 p-2 pl-5 pr-5"
          >
            BACK
          </button>
        </router-link>
      </div>
    </div>
  </main>
</template>
<script>
import {
  getOrderDetails,
  approveReturnOrderApi,
} from "../../services/orderMgmntApis";
export default {
  data() {
    return {
      orderDetails: {},
      codeStatus: {
        1: "Submitted",
        2: "InTransit",
        3: "Delivered",
        4: "Closed",
      },
      packageType: {
        1: "Reel",
        2: "Carton",
        3: "Pallet",
      },
      comment: "",
    };
  },
  created() {
    this.getOrderDetails();
  },
  methods: {
    getOrderDetails() {
      let orderID = this.$route.params.orderId;
      getOrderDetails(orderID)
        .then((result) => {
          this.orderDetails = result.data;
        })
        .catch((err) => {
          console.log(err)
        });
    },
    approveReturnOrder() {
      approveReturnOrderApi(this.$route.params.orderId, this.comment)
        .then((result) => {
          if ('status' in result && result.status == 200) {
            this.$router.push({ name: 'ReturnOrderListing'})
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  components: {},
};
</script>
